"use client";

import { useState } from "react";
import { CameraModal } from "./CameraModal";
import { DiscoveryPath } from "./DiscoveryPath";
import { ElementDetails } from "./ElementDetails";
import { ScrollArea } from "@/components/ui/scroll-area";
import { Card } from "@/components/ui/card";
import { Camera, LockIcon } from "lucide-react";
import { heritageElements } from "@/lib/data/heritage-elements"; // Import your heritage elements

export default function HeritageHunt() {
  const [isCameraOpen, setIsCameraOpen] = useState(false);
  const [selectedElement, setSelectedElement] = useState(null);
  const [filter, setFilter] = useState<"all" | "locked" | "unlocked">("all");
  const [elements, setElements] = useState(heritageElements); // Manage heritage elements state

  const filteredElements = elements.filter((element) => {
    if (filter === "all") return true;
    return filter === "unlocked" ? element.unlocked : !element.unlocked;
  });

  // Function to update an element's unlocked status
  const unlockElement = (id: string) => {
    setElements((prevElements) =>
      prevElements.map((element) =>
        element.id === id ? { ...element, unlocked: true } : element
      )
    );

    // Update selected element if it's currently selected
    if (selectedElement?.id === id) {
      setSelectedElement((prev) => ({ ...prev, unlocked: true }));
    }
  };

  return (
    <div className="min-h-screen">
      {/* Header Section */}
      <div className="h-[40vh] relative bg-gradient-to-b from-stone-900 to-transparent">
        <div className="absolute inset-0 flex items-center justify-center text-center p-4">
          <div className="space-y-4">
            <h1 className="text-5xl font-serif text-amber-100">
              Discover Sbeitla
            </h1>
            <p className="text-xl text-amber200/80 max-w-2xl">
              Uncover the hidden treasures of ancient Sufetula
            </p>

            {/* Filter Buttons */}
            <div className="flex justify-center space-x-4 mt-4">
              {["all", "locked", "unlocked"].map((option) => (
                <button
                  key={option}
                  onClick={() =>
                    setFilter(option as "all" | "locked" | "unlocked")
                  }
                  className={`px-4 py-2 rounded ${
                    filter === option ? "bg-blue-600" : "bg-gray-600"
                  } text-white`}
                >
                  {option.charAt(0).toUpperCase() + option.slice(1)}
                </button>
              ))}
            </div>
          </div>
        </div>
      </div>

      {/* Main Content */}
      <div className="container mx-auto px-4 -mt-[20px]">
        {/* Discovery Timeline/Path */}
        <DiscoveryPath
          elements={filteredElements}
          onElementSelect={setSelectedElement}
          selectedElement={selectedElement}
        />

        {/* Selected Element Details */}
        {selectedElement && (
          <div className="mt-[30px]">
            <ElementDetails
              element={selectedElement}
              onScan={() => setIsCameraOpen(true)}
            />
          </div>
        )}
      </div>

      {/* Camera Modal */}
      <CameraModal
        open={isCameraOpen}
        onOpenChange={setIsCameraOpen}
        selectedElement={selectedElement}
        onUnlockElement={() => unlockElement(selectedElement.id)} // Pass unlock logic
      />
    </div>
  );
}
