"use client";

import { Card } from "@/components/ui/card";
import { Camera, Map, History, Info, Lock, CheckCircle2 } from "lucide-react";
import { cn } from "@/lib/utils";

interface ElementDetailsProps {
  element: {
    id: string;
    name: string;
    description?: string;
    location?: {
      area?: string;
      hints?: string[];
    };
    clues?: {
      silhouette?: string;
      riddle?: string;
      historicalContext?: string;
    };
    unlocked?: boolean; // Track if the element is unlocked
    targetImagePath?: string; // Path for the colored image
  };
  onScan: () => void;
}

export function ElementDetails({ element, onScan }: ElementDetailsProps) {
  if (!element) {
    return <div>Loading...</div>;
  }

  // Safely determine image paths
  const beforeImagePath =
    element.targetImagePath?.replace("after", "before") ||
    "/path/to/default-before.jpg";
  const displayImagePath = element.unlocked
    ? element.targetImagePath // Use "after" image when unlocked
    : beforeImagePath; // Use "before" image otherwise

  return (
    <div className="grid md:grid-cols-[1.5fr,1fr] gap-8">
      {/* Left Image Section */}
      <div className="space-y-6">
        <Card className="overflow-hidden bg-stone-900/50 border-amber-900/20">
          <div className="aspect-video relative">
            <img
              src={displayImagePath}
              alt={element.name || "Unknown Element"}
              className={cn(
                "w-full h-full object-cover transition-all duration-500",
                element.unlocked
                  ? "brightness-100 contrast-100 saturate-100"
                  : "brightness-50 contrast-75 saturate-50 grayscale"
              )}
            />
            {!element.unlocked && (
              <div className="absolute inset-0 bg-gradient-to-t from-stone-950/80 to-transparent" />
            )}
            {/* Lock/Unlock Badge */}
            <div className="absolute top-2 right-2 p-2 bg-stone-900/80 rounded-full">
              {element.unlocked ? (
                <CheckCircle2 className="w-6 h-6 text-green-500" />
              ) : (
                <Lock className="w-6 h-6 text-amber-500" />
              )}
            </div>
          </div>
        </Card>

        {!element.unlocked && (
          <button
            onClick={onScan}
            className="w-full p-4 bg-amber-900/20 rounded-lg border border-amber-900/20 hover:bg-amber-900/40 transition-colors flex items-center justify-center gap-2"
          >
            <Camera className="w-6 h-6 text-amber-500" />
            <span className="text-amber-100">Scan to Discover</span>
          </button>
        )}
      </div>

      {/* Right Details Section */}
      <div className="space-y-6">
        <h2 className="text-xl font-serif text-white">{element.name}</h2>
        {element.unlocked ? (
          <>
            <p>{element.description}</p>
            <p>{element.clues?.historicalContext}</p>
          </>
        ) : (
          <>
            <p>{element.clues?.riddle}</p>
            <ul>
              {element.location?.hints?.map((hint, index) => (
                <li key={index}>{hint}</li>
              ))}
            </ul>
          </>
        )}
        <p>Location: {element.location?.area}</p>
      </div>
    </div>
  );
}
